import React from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, HStack
} from '@chakra-ui/react';
import Icon from '../../../components/Icon';
import style from './PaymentMethod.module.css';
import MiniBox from '../../../layout/MiniBox';
import { useDrawer } from '../../../hooks/drawer';

const IntraBank = React.lazy(() => import('../Intrabank/Intrabank'));
const Check = React.lazy(() => import('../Check/Check'));
const Ach = React.lazy(() => import('../Ach/Ach'));
const DomesticWireAddress = React.lazy(() => import('../DomesticWire/DomesticWireAddress'));
const InternationalWireAddress = React.lazy(() => import('../InternationalWire/InternationalWireAddress'));
const SendCard = React.lazy(() => import('../SendCard/SendCard'));

const PaymentMethod = () => {
  const { t } = useTranslation();
  const { open } = useDrawer();

  return (
    <Box className="page">
      <MiniBox
        heading={t('payment_methods.paymentMethods')}
      >
        <Box width="100%" paddingTop="15px">
          <Box pb="20px">

            <HStack className={style.action} role="button" onClick={() => open(IntraBank)}>
              <Box>
                <Text variant="regular">{t('payment_methods.intrabank')}</Text>
              </Box>
              <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
            </HStack>

            <HStack className={style.action} role="button" onClick={() => open(Check)}>
              <Box>
                <Text variant="regular">{t('payment_methods.check')}</Text>
              </Box>
              <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
            </HStack>

            <HStack className={style.action} role="button" onClick={() => open(Ach)}>
              <Box>
                <Text variant="regular">{t('payment_methods.ach')}</Text>
              </Box>
              <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
            </HStack>

            <HStack className={style.action} role="button" onClick={() => open(DomesticWireAddress)}>
              <Box>
                <Text variant="regular">{t('payment_methods.domesticWire')}</Text>
              </Box>
              <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
            </HStack>

            <HStack className={style.action} role="button" onClick={() => open(InternationalWireAddress)}>
              <Box>
                <Text variant="regular">{t('payment_methods.internationalWire')}</Text>
              </Box>
              <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
            </HStack>

            <HStack className={style.action} role="button" onClick={() => open(SendCard)}>
              <Box>
                <Text variant="regular">{t('payment_methods.sendVisaCard')}</Text>
              </Box>
              <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
            </HStack>

          </Box>

        </Box>
      </MiniBox>
    </Box>
  );
};

export default PaymentMethod;
